import React, { useState } from 'react';
import { Container } from '@material-ui/core/';

import SearchInput from './search-input/SearchInput';
import SearchRsults from './search-results/SearchResults';
import SearchPagination from './search-pagination/SearchPagination';

export default function SearchSicha() {
  const [sichos, setSichos] = useState();
  const [count, setCount] = useState();
  const [page, setPage] = useState(1);
  
  const getSichos = (sichos) => {
    setSichos(sichos);
  }

  const getCount = (count) => {
    setCount(count);
  }

  const handlePage = (value) => {
    setPage(value)
  }

  return (
    <Container className="container">
      <SearchInput getCount={getCount} getSichos={getSichos} page={page} count={count} />
      <SearchRsults sichos={sichos} />
      {!!count && <SearchPagination count={count} page={page} handlePage={handlePage} />}
    </Container>
  )
}