import { Route, Switch } from 'react-router-dom';
import Admin from '../components/admin/Admin';
import DailySicha from '../components/daily-sicha/DailySicha';
import Topics from '../components/topics/Topics';
import UploadDS from '../components/admin/upload-dailly-sicha/UploadDS';
import UploadSichosFiles from '../components/admin/upload-sichos-files/UploadSichosFiles';
import Managments from '../components/admin/management/Management';
import Dedications from '../components/admin/dedications/Dedicaions';
import PdfCheck from '../components/admin/pdfCheck/PdfCheck';
import SearchSicha from '../components/search-sicha/searchSicha';
import UploadTopics from '../components/admin/upload-topics/UploadTopics';
import SignIn from '../components/login/Login'
import Dedication from '../components/dedication/Dedication';
import Contact from '../components/contact/Contact';
import ProtectedRoute from './ProtectedRoute';
import { Container } from '@material-ui/core';

const children = (
  <>
    <Admin />
    <Container className="container">
      <Route path="/admin" exact>
        <UploadDS />
      </Route>
      <Route path="/admin/upload-files" exact>
        <UploadSichosFiles />
      </Route>
      <Route path="/admin/topics" exact>
        <UploadTopics />
      </Route>
      <Route path="/admin/pdf-check" exact>
        <PdfCheck />
      </Route>
      <Route path="/admin/manage" exact>
        <Managments />
      </Route>
      <Route path="/admin/dedications" exact>
        <Dedications />
      </Route>
    </Container>
  </>
)

export default function RoutenoConfig(props) {
  return (
    <Switch>
      <Route path="/" exact={true}>
        <DailySicha setSpinner={props.setSpinner} />
      </Route>
      <Route path="/search">
        <SearchSicha />
      </Route>
      <Route path="/topics">
        <Topics />
      </Route>
      <Route path="/dedication">
        <Dedication setSpinner={props.setSpinner} />
      </Route>
      <Route path="/contact">
        <Contact setSpinner={props.setSpinner} />
      </Route>
      <Route path="/login">
        <SignIn setAuth={props.setAuth} />
      </Route>
      <ProtectedRoute path="/admin" component={Admin} child={children} />
      <Route path="*">
      </Route>
    </Switch>
  )
}