import React, { useState } from 'react';
import { useStyles, submit } from './managementService';
import './management.css';
import { Button, Typography, InputLabel, Input } from '@material-ui/core/';
import TextField from '@material-ui/core/TextField';
import { create } from "jss";
import rtl from "jss-rtl";
import { StylesProvider, jssPreset, createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import Loader from "react-loader-spinner";
const jss = create({ plugins: [...jssPreset().plugins, rtl()] });

const rtlTheme = createMuiTheme({ direction: "rtl" });



export default function Management() {
  const classes = useStyles();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const handleNamechange = (e) => {
    setEmail(e.target.value)
  }

  const handlePasswordchange = (e) => {
    setPassword(e.target.value)
  }


  return (
    <StylesProvider jss={jss}>
      <ThemeProvider theme={rtlTheme} >
        <h3> </h3>
        <form className={classes.root} noValidate autoComplete="off">
          <TextField
            label='"'
            type="email"
            autoComplete="current-password"
            variant="outlined"
            value={email}
            onChange={handleNamechange}
          />
          <TextField
            label=""
            type="password"
            autoComplete="current-password"
            variant="outlined"
            value={password}
            onChange={handlePasswordchange}
          />
          <Button variant="contained" color="primary" onClick={() => submit(email, password)} ></Button>
        </form>
      </ThemeProvider>
    </StylesProvider>
  );
}
