import React, { useEffect, useState } from 'react'
import { AppBar, Menu, MenuItem, Toolbar, Typography, IconButton, Box, Drawer, useMediaQuery } from '@material-ui/core';
import { Link } from "react-router-dom";
import SearchIcon from '@material-ui/icons/Search';

import { useStyles, getTopicTitles } from './headerService'
import './header.css';
import { logout } from '../login/loginService';

export default function Header(props) {
  const classes = useStyles();
  const format = { year: 'numeric', month: "2-digit", day: "2-digit" };
  const date = new Date().toLocaleDateString('he-IL', format).replace(/\./g, '-');
  const [anchorEl, setAnchorEl] = useState(null);
  const [topics, setTopics] = useState([]);
  const [topicsList, setTopicsList] = useState([]);
  const matches = useMediaQuery('(min-width:470px)');

  useEffect(() => {
    getTopicTitles().then(res => {
      setTopics(res);
    })
  }, []);

  useEffect(() => {
    const TL = topics?.map(topic => (
      <MenuItem key={topic._id} onClick={handleClose}>
        <Link className="link topic-link" to={`/topics?id=${topic._id}`}>
          {topic.topic}
        </Link>
      </MenuItem>
    ))
    setTopicsList(TL);
  }, [topics]);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <>
      <div className="img-top"></div>

      <AppBar className="nav-bar" position="static">
        <Toolbar className="toolbar">
          <Typography variant="h4" className={classes.title}>
            <div className="bsd">"</div>
            <Link className="link title" to={`/?date=${date}`}>
               
            </Link>
          </Typography>
          {props.auth && <>
            <Typography className="bar-link" variant="h5">
              <Link className="link" to="/admin">
                
              </Link>
            </Typography>
            <Typography className="bar-link" variant="h5">
              <Link className="link" to="/" onClick={() => logout(props.setAuth)}>
                
              </Link>
            </Typography>
          </>}
          {/* <Typography className="bar-link" variant="h5">
            <Link className="link" to="/contact">
               
            </Link>
          </Typography> | */}
          <Typography className="bar-link" variant="h5">
            <Link className="link" to="/dedication">
              
            </Link>
          </Typography> |
          <Typography className="bar-link" variant="h5">
            {matches ? <Box className="link" onClick={handleClick} ></Box> :
              <IconButton className="link" onClick={handleClick} >
                <svg xmlns="http://www.w3.org/2000/svg" width="25" height="25" fill="currentColor" class="bi bi-card-list" viewBox="0 0 16 16">
                  <path d="M14.5 3a.5.5 0 0 1 .5.5v9a.5.5 0 0 1-.5.5h-13a.5.5 0 0 1-.5-.5v-9a.5.5 0 0 1 .5-.5h13zm-13-1A1.5 1.5 0 0 0 0 3.5v9A1.5 1.5 0 0 0 1.5 14h13a1.5 1.5 0 0 0 1.5-1.5v-9A1.5 1.5 0 0 0 14.5 2h-13z" />
                  <path d="M5 8a.5.5 0 0 1 .5-.5h7a.5.5 0 0 1 0 1h-7A.5.5 0 0 1 5 8zm0-2.5a.5.5 0 0 1 .5-.5h7a.5.5 0 0 1 0 1h-7a.5.5 0 0 1-.5-.5zm0 5a.5.5 0 0 1 .5-.5h7a.5.5 0 0 1 0 1h-7a.5.5 0 0 1-.5-.5zm-1-5a.5.5 0 1 1-1 0 .5.5 0 0 1 1 0zM4 8a.5.5 0 1 1-1 0 .5.5 0 0 1 1 0zm0 2.5a.5.5 0 1 1-1 0 .5.5 0 0 1 1 0z" />
                </svg>
              </IconButton>}
            <Menu
              disableScrollLock={true}
              className={classes.menu + ' topic-menu'}
              id="simple-menu"
              anchorEl={anchorEl}
              keepMounted
              open={Boolean(anchorEl)}
              onClose={handleClose}
            >
              {topicsList}
            </Menu>

          </Typography> |
          {matches ? <Typography className="bar-link" variant="h5">
            <Link className="link" to="/search">
              
            </Link>
          </Typography> :
            <IconButton color="inherit">
              <Link to="/search">
                <SearchIcon className="link" />
              </Link>
            </IconButton>}
        </Toolbar>
      </AppBar>
    </>
  )
}

