import { makeStyles } from '@material-ui/core/styles';
import mammoth from 'mammoth';
import axios from 'axios';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    textAlign: 'center',
  },
  form: {
    '& .MuiTextField-root': {
      margin: theme.spacing(1),
      width: '60ch',
      display: 'flex',
    },
    marginBottom: '15px',
  },
  input: {
    marginRight: 'auto !importent',
  },
}));


function wordToHTML(files) {
  return new Promise(resolve => {
    const obj = {};
    Array.from(files).forEach((file, i)=> {
      const reader = new FileReader();
      reader.onloadend = async function () {
        const res = await mammoth.convertToHtml({ arrayBuffer: reader.result });
        obj[file.name.split('.')[0]] = res.value;
        if (i + 1 === files.length) {
          resolve(obj);
        }
      }
      reader.readAsArrayBuffer(files[i]);
    });
  });
}

function setFilesObj(files) {
  const obj = {};
  Array.from(files).forEach((file, i)=> {
    obj[file.name.split('.')[0]] = file;
  });
  return obj;
}

async function submit(topic, abst, cont, contHeb, rec) {
  const { data } = await axios.get(`${process.env.REACT_APP_API_ENDPOINT}/daily-sicha/get-base-url`);

  const arr = [];
  for (const o in abst) {
    arr.push({
      title: o,
      abstract: abst[o],
      content: `${data}/public/pdf/${cont[o].name}`,
      contentHeb: `${data}/public/pdfHeb/${contHeb[o].name}`,
      recUrl: `${data}/public/records/${rec[o].name}`
    })
  }
  rec = Object.values(rec).map(val => val);
  cont = Object.values(cont).map(val => val);
  contHeb = Object.values(contHeb).map(val => val);
  const formData = new FormData();
  formData.append('topic', topic);
  formData.append('docs', JSON.stringify(arr));
  rec.forEach(file => {
    formData.append('recs', file, file.name );
  })
  cont.forEach(file => {
    formData.append('cont', file, file.name );
  })
  contHeb.forEach(file => {
    formData.append('contHeb', file, file.name );
  })
  axios.post(`${process.env.REACT_APP_API_ENDPOINT}/topic/add-topic`, formData).then(res => {
    console.log(res);
  })
}



export { useStyles, wordToHTML, submit, setFilesObj };