/* eslint-disable jsx-a11y/alt-text */
import React, { useState } from 'react';
import { Button, CssBaseline, TextField, Grid, Box, Typography, Container, InputAdornment } from '@material-ui/core';
import CommentRoundedIcon from '@material-ui/icons/CommentRounded';
import MailRoundedIcon from '@material-ui/icons/MailRounded';
import PersonRoundedIcon from '@material-ui/icons/PersonRounded';
import ShortTextRoundedIcon from '@material-ui/icons/ShortTextRounded';
import PhoneInTalkRoundedIcon from '@material-ui/icons/PhoneInTalkRounded';
import { useStyles, submit } from './contactService';
import { create } from "jss";
import rtl from "jss-rtl";
import { StylesProvider, jssPreset, createMuiTheme, ThemeProvider, makeStyles } from '@material-ui/core/styles';
import './contact.css';

const jss = create({ plugins: [...jssPreset().plugins, rtl()] });
const rtlTheme = createMuiTheme({ direction: "rtl" });
export default function Contact() {
  const classes = makeStyles();
  const [fullName, setFullName] = useState('');
  const [email, setEmail] = useState('');
  const [subject, setSubject] = useState('');
  const [message, setMessage] = useState('');

  return (
    <Container className="container dedication" component="main" maxWidth="md">
      <StylesProvider jss={jss}>
        <ThemeProvider theme={rtlTheme} >
          <CssBaseline />
          <Box className={classes.paper}>
            <Typography component="h1" variant="h5">
               
            </Typography>
            <form className={classes.form}>
              <Grid container spacing={2}>
                <Grid item xs={6}>
                  <TextField
                    required
                    variant="outlined"
                    fullWidth
                    id="fullName"
                    label=' '
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <PersonRoundedIcon />
                        </InputAdornment>
                      ),
                    }}
                    autoFocus
                    value={fullName}
                    onChange={(e) => setFullName(e.target.value)}
                  />
                </Grid>
                <Grid item xs={6}>
                  <TextField
                    required
                    variant="outlined"
                    fullWidth
                    label=' "'
                    type="email"
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="atart">
                          <MailRoundedIcon />
                        </InputAdornment>
                      ),
                    }}
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    required
                    variant="outlined"
                    fullWidth
                    id="address"
                    label=''
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <ShortTextRoundedIcon />
                        </InputAdornment>
                      ),
                    }}
                    value={subject}
                    onChange={(e) => setSubject(e.target.value)}
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    className="comment"
                    variant="outlined"
                    fullWidth
                    multiline
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <CommentRoundedIcon />
                        </InputAdornment>
                      ),
                    }}
                    rows={3}
                    label=' '
                    value={message}
                    onChange={(e) => setMessage(e.target.value)}
                  />
                </Grid>
              </Grid>
              <Button
                type="submit"
                variant="contained"
                color="primary"
              >
                
              </Button>
            </form>
          </Box>
          <Grid container>
            <Grid>
              <a href="tel:+13477092770" target="_blank" rel="noreferrer">
                <PhoneInTalkRoundedIcon fontSize="large" />
              </a>
              <a href="mailto:dailysicha@gmail.com" target="_blank" rel="noreferrer">
                <MailRoundedIcon fontSize="large" />
              </a>
              <a href="https://api.whatsapp.com/send?phone=13477092770" target="_blank" rel="noreferrer">
                <svg xmlns="http://www.w3.org/2000/svg" width="50" height="50" viewBox="0 0 24 24"><path d="M.057 24l1.687-6.163c-1.041-1.804-1.588-3.849-1.587-5.946.003-6.556 5.338-11.891 11.893-11.891 3.181.001 6.167 1.24 8.413 3.488 2.245 2.248 3.481 5.236 3.48 8.414-.003 6.557-5.338 11.892-11.893 11.892-1.99-.001-3.951-.5-5.688-1.448l-6.305 1.654zm6.597-3.807c1.676.995 3.276 1.591 5.392 1.592 5.448 0 9.886-4.434 9.889-9.885.002-5.462-4.415-9.89-9.881-9.892-5.452 0-9.887 4.434-9.889 9.884-.001 2.225.651 3.891 1.746 5.634l-.999 3.648 3.742-.981zm11.387-5.464c-.074-.124-.272-.198-.57-.347-.297-.149-1.758-.868-2.031-.967-.272-.099-.47-.149-.669.149-.198.297-.768.967-.941 1.165-.173.198-.347.223-.644.074-.297-.149-1.255-.462-2.39-1.475-.883-.788-1.48-1.761-1.653-2.059-.173-.297-.018-.458.13-.606.134-.133.297-.347.446-.521.151-.172.2-.296.3-.495.099-.198.05-.372-.025-.521-.075-.148-.669-1.611-.916-2.206-.242-.579-.487-.501-.669-.51l-.57-.01c-.198 0-.52.074-.792.372s-1.04 1.016-1.04 2.479 1.065 2.876 1.213 3.074c.149.198 2.095 3.2 5.076 4.487.709.306 1.263.489 1.694.626.712.226 1.36.194 1.872.118.571-.085 1.758-.719 2.006-1.413.248-.695.248-1.29.173-1.414z" /></svg>
              </a>

            </Grid>
          </Grid>
        </ThemeProvider>
      </StylesProvider>
    </Container >
  )
}