import { makeStyles } from '@material-ui/core/styles';
import axios from 'axios';

const useStyles = makeStyles((theme) => ({
  root: {
    '& .MuiTextField-root': {
      margin: theme.spacing(1),
      width: '25ch',
      display: 'flex',
    },
  },
}));



function submit(email, password) {
  axios.post(`${process.env.REACT_APP_API_ENDPOINT}/auth/add-admin`, {email, password}).then(res => {
    console.log(res);
  })
}



export { useStyles, submit };