/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from 'react';
import { Button, CssBaseline, TextField, Grid, Box, Typography, Container } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import Paypal from '../paypal/Paypal';
import { create } from "jss";
import rtl from "jss-rtl";
import { StylesProvider, jssPreset, createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import { useStyles, submit } from './dedicationService';
import { svg } from './svg';
import './dedication.css';

const jss = create({ plugins: [...jssPreset().plugins, rtl()] });
const rtlTheme = createMuiTheme({ direction: "rtl" });

export default function Dedication(props) {
  const classes = useStyles();
  const [fullName, setFullName] = useState('');
  const [address, setAddress] = useState('');
  const [email, setEmail] = useState('');
  const [phone, setPhone] = useState('');
  const [dedications, setDedications] = useState([]);
  const [dedicationsValue, setDedicationsValue] = useState([]);
  const [notes, setNotes] = useState('');
  const [pay, setPay] = useState(false);
  const [modal, setModal] = useState(false);

  useEffect(() => {
    addDedication()
  }, []);

  function displayPayment(e) {
    e.preventDefault();
    setPay(true)
  }

  const dedicationValue = {
    date: '',
    event: '',
    name: '',
    parent: '',
    donor: '',
  }

  const onChange = (i, e) => {
    const newArr = [...dedicationsValue];
    newArr[i][e.target.name] = e.target.value;
    setDedicationsValue(newArr);
    console.log(dedicationsValue);
  }

  const removeDedication = (i) => {
    const newArr = [...dedications];
    const newArr2 = [...dedicationsValue];
    newArr.splice(i, 1)
    newArr2.splice(i, 1)
    setDedications(newArr);
    setDedicationsValue(newArr2);
    console.log(dedicationsValue);
  }

  const addDedication = () => {
    setDedications(state => [...state, '']);
    setDedicationsValue(state => [...state, dedicationValue]);
    setPay(false);
  }

  const onSubmit = () => {
    props.setSpinner(true);
    submit(fullName, address, email, phone, dedicationsValue, notes).then(res => {
      props.setSpinner(false);
      setModal(true);
      reset()
    });
  }
  const reset = () => {
    setFullName('');
    setAddress('');
    setEmail('');
    setPhone('');
    setDedications(['']);
    setDedicationsValue([dedicationValue]);
    // addDedication();
    setNotes('');
    setPay(false);
  }

  return (
    <Container className="container dedication" component="main" maxWidth="md">
      <StylesProvider jss={jss}>
        <ThemeProvider theme={rtlTheme} >
          <CssBaseline />
          <div className={classes.paper}>
            {svg}
            <Typography component="h1" variant="h5">
                " "
            </Typography>
            <form className={classes.form} onSubmit={displayPayment}>
              <Grid container spacing={3}>
                <Grid item xs={12}>
                  <h3> </h3>
                </Grid>
                <Grid item xs={6}>
                  <TextField
                    required
                    variant="outlined"
                    margin="normal"
                    fullWidth
                    id="fullName"
                    label=' '
                    name="fullName"
                    autoComplete="fullName"
                    autoFocus
                    value={fullName}
                    onChange={(e) => setFullName(e.target.value)}
                  />
                </Grid>
                <Grid item xs={6}>
                  <TextField
                    required
                    variant="outlined"
                    margin="normal"
                    fullWidth
                    id="address"
                    label=''
                    name="address"
                    autoComplete="address"
                    value={address}
                    onChange={(e) => setAddress(e.target.value)}
                  />
                </Grid>
                <Grid item xs={6}>
                  <TextField
                    required
                    variant="outlined"
                    margin="normal"
                    fullWidth
                    id="email"
                    label=' "'
                    type="email"
                    name="email"
                    autoComplete="email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                  />
                </Grid>
                <Grid item xs={6}>
                  <TextField
                    required
                    variant="outlined"
                    margin="normal"
                    fullWidth
                    name="phone"
                    label=""
                    type="tel"
                    id="phone"
                    autoComplete="phone"
                    value={phone}
                    onChange={(e) => setPhone(e.target.value)}
                  />
                </Grid>
                <Grid item xs={12}>
                  <h3>  </h3>
                </Grid>
                {dedications && dedications.map((el, i) => (
                  <Grid className="ded-obj" container spacing={3} key={'key' + i}>
                    <Grid item container alignItems="center" xs={12}>
                      <Grid item xs={6}>
                        {dedications.length > 1 && <h4> ' {i + 1}</h4>}
                      </Grid>
                      <Grid item xs={6} className="delete">
                        {i !== 0 && <Button onClick={() => removeDedication(i)}>
                          <DeleteForeverIcon />
                        </Button>}
                      </Grid>
                    </Grid>
                    <Grid item xs={6}>
                      <TextField
                        required
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        id="date"
                        label=''
                        name="date"
                        autoComplete="date"
                        value={dedicationsValue[i].date}
                        onChange={(e) => onChange(i, e)}
                      />
                    </Grid>
                    <Grid item xs={6}>
                      <TextField
                        required
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        id="event"
                        label=' // '
                        name="event"
                        autoComplete="event"
                        value={dedicationsValue[i].event}
                        onChange={(e) => onChange(i, e)}
                      />
                    </Grid>
                    <Grid item xs={6}>
                      <TextField
                        required
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        id="name"
                        label=''
                        name="name"
                        autoComplete="name"
                        value={dedicationsValue[i].name}
                        onChange={(e) => onChange(i, e)}
                      />
                    </Grid>
                    <Grid item xs={6}>
                      <TextField
                        required
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        id="parent"
                        label=' /'
                        name="parent"
                        autoComplete="parent"
                        value={dedicationsValue[i].parent}
                        onChange={(e) => onChange(i, e)}
                      />
                    </Grid>
                    <Grid item xs={6}>
                      <TextField
                        required
                        variant="outlined"
                        margin="normal"
                        fullWidth
                        id="donor"
                        label=''
                        name="donor"
                        autoComplete="donor"
                        value={dedicationsValue[i].donor}
                        onChange={(e) => onChange(i, e)}
                      />
                    </Grid>
                  </Grid>))}
                <Grid item xs={12}>
                  <Button variant="outlined" color="primary" onClick={addDedication}>
                    <AddIcon />
                     
                  </Button>
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    className="notes"
                    variant="outlined"
                    multiline
                    rows={3}
                    value={notes}
                    label=''
                    onChange={(e) => setNotes(e.target.value)}
                  />
                </Grid>
              </Grid>
              <Box marginTop={5} textAlign="center" fontSize="larger">
                 :  " "     ,     " "   
              </Box>
              <Box margin={1} textAlign="center" fontWeight={600} fontSize="large">
                  - 75$
              </Box>
              <Box textAlign="center">
                {!pay ? <Button
                  type="submit"
                  fullWidth
                  variant="contained"
                  color="primary"
                  className={classes.submit}
                >
                   
                </Button> :
                  <Paypal amount={75 * dedications.length} submit={onSubmit} />}</Box>

            </form>
          </div>
        </ThemeProvider>
      </StylesProvider>
      {modal && <Box className="modal-wrapper" onClick={() => setModal(false)} textAlign="center">
        <Typography onClick={(e) => e.stopPropagation()} className="modal-body" variant="h6">
          <span onClick={() => setModal(false)} className="close">&times;</span>
            
        </Typography>
      </Box>}
    </Container >
  )
}
