import React, { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import { Box, Typography } from '@material-ui/core';

import { getTopicSicha, getSearchSicha } from './dailySichaService';
import DatePicker from './datePicker/DatePicker';
import Sicha from './sicha/Sicha';
import './dailySicha.css';

export default function DailySicha (props) {
  const location = useLocation()
  const [sicha, setSicha] = useState();
  const [notFound, setNotFound] = useState(false);
  const [mode, setMode] = useState();
  const [extended, setExtended] = useState(false);
  const getSicha = (sicha) => {
    setSicha(sicha);
  }

  useEffect(() => {
    if (!window.location.search || window.location.search.includes('date')) {
      setMode('ds');
    } else if(!window.location.search || window.location.search.includes('sicha')) {
      setMode('sicha');
      const id = window.location.search.split('=')[1];
      getSearchSicha(id).then(res => {
        console.log(res);
        setSicha(res);
      });
    } else if (window.location.search.includes('id')) {
      const id  = window.location.search.split('=')[1];
      getTopicSicha(id).then(res => {
        setSicha(res[0]);
      });
    }
    return () => {
      setSicha(null)
      setMode(null);
    }
  }, [location])

  return (
    <div className="sicha-wrapper">
      {mode === 'ds' ? <DatePicker sicha={sicha} extended={extended} setNotFound={setNotFound} setSpinner={props.setSpinner} getSicha={getSicha} /> : 
      mode === 'sicha' ? <div className="date-wrapper">
      <Box className="date-picker">
        <Typography variant="h6" className="date" >{sicha && ' ' + sicha.deliveredDate + " " + sicha.deliveredYear}</Typography>
      </Box>
    </div> :
      <div className="date-wrapper">
      <Box className="date-picker">
        <Typography variant="h6" className="date" >{sicha?.title}</Typography>
      </Box>
    </div>}
      {notFound && <Box paddingTop="55px" color="#969696"  fontSize="25px" textAlign="center">
            
      </Box>}
      <Sicha sicha={sicha} extended={extended} setExtended={setExtended} />
      {sicha &&<Box className="distance"></Box>}
    </div>
  )
}