import React, { useState } from 'react';
import { Avatar, Button, CssBaseline, TextField, FormControlLabel, Checkbox, Link, Grid, Typography, Container } from '@material-ui/core';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import { create } from "jss";
import rtl from "jss-rtl";
import { StylesProvider, jssPreset, createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import { useStyles, submit } from './loginService';
import { useHistory} from 'react-router-dom';

const jss = create({ plugins: [...jssPreset().plugins, rtl()] });
const rtlTheme = createMuiTheme({ direction: "rtl" });

export default function SignIn(props) {
  const classes = useStyles();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [remember, setRemember] = useState(false);
  const history = useHistory();


  function onSubmit(e) {
    e.preventDefault();
    submit(email, password, remember, history, props.setAuth)
  }

  return (
    <Container className="container" component="main" maxWidth="xs">
      <StylesProvider jss={jss}>
        <ThemeProvider theme={rtlTheme} >
        <CssBaseline />
        <div className={classes.paper}>
          <Avatar className={classes.avatar}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            
          </Typography>
          <form className={classes.form} noValidate>
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="email"
              label=' "'
              name="email"
              autoComplete="email"
              autoFocus
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              name="password"
              label=""
              type="password"
              id="password"
              autoComplete="current-password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
            <FormControlLabel
              control={<Checkbox value={remember}
                onChange={(e) => setRemember(e.target.value)}
                color="primary" />}
              label=" "
            />
            <Button
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
              onClick={onSubmit}
            >
              
            </Button>
            <Grid container>
              <Grid item xs>
                <Link href="#" variant="body2">
                   ?
                </Link>
              </Grid>
            </Grid>
          </form>
        </div>
      </ThemeProvider>
      </StylesProvider>
    </Container >
  );
}