import React, { useEffect, useState } from 'react';
import { Card, CardActionArea, CardContent, Container, Grid, Typography } from '@material-ui/core/';
import { Link, useLocation } from 'react-router-dom';
import { getTopics, useStyles } from './topicsService';

export default function Topics() {
  const location = useLocation()
  const classes = useStyles();
  const [topic, setTopic] = useState();

  useEffect(() => {
    if (window.location.search.startsWith('?id')) {
      const id = window.location.search.split('=')[1];
      getTopics(id).then(res => {
        setTopic(res);
      })
    }
  }, [location]);

  const sichosList = topic?.sichos.map(sicha => (
    <Grid item xs={4} key={sicha._id}>
      <Link className={classes.link} to={`/?id=${sicha._id}`}>
        <Card className={classes.root}>
          <CardActionArea className={classes.content}>
            <CardContent>
              <Typography className="text" gutterBottom variant="h5" component="h2">
                {sicha.title}
              </Typography>
              <Typography className="text" variant="body2" color="textSecondary" component="p"
                dangerouslySetInnerHTML={{ __html: sicha.abstract }}
              >
              </Typography>
            </CardContent>
            <div className="press">  </div>
          </CardActionArea>
        </Card>
      </Link>
    </Grid>
  ));

  return (
    <Container className="container">
      <h1>{topic?.topic}</h1>
      <Grid container spacing={3}>
        {sichosList}
      </Grid>
    </Container>
  )
}