import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { Paper, Tabs, Tab } from '@material-ui/core';

import './admin.css';

export default function Admin() {
  const [value, setValue] = useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  return (
    <>
      <Paper square>
        <Tabs value={value}
          onChange={handleChange}
          indicatorColor="primary"
          textColor="primary"
        >
          <Tab
            label="  "
            className="tabLink"
            component={Link}
            to="/admin"
          />
          <Tab
            label="  "
            className="tabLink"
            component={Link}
            to="/admin/upload-files"
          />
          <Tab
            label=" "
            className="tabLink"
            component={Link}
            to="/admin/topics"
          />
          <Tab
            label=" pdf"
            className="tabLink"
            component={Link}
            to="/admin/pdf-check"
          />
          <Tab
            label=""
            className="tabLink"
            component={Link}
            to="/admin/manage"
          />
          <Tab
            label=""
            className="tabLink"
            component={Link}
            to="/admin/dedications"
          />
        </Tabs>
      </Paper>
    </>
  )
}
