/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect, useRef } from 'react';
import { Box, IconButton, Tooltip } from '@material-ui/core';
import PlayCircleOutlineIcon from '@material-ui/icons/PlayCircleOutline';
import PauseCircleOutlineIcon from '@material-ui/icons/PauseCircleOutline';
import GetAppIcon from '@material-ui/icons/GetApp';
import Replay10Icon from '@material-ui/icons/Replay10';
import Forward10Icon from '@material-ui/icons/Forward10';
import './audioPlayer.css';

export default function AudioPlayer(props) {
  const [isPlaying, setIsPlaying] = useState(false);
  const [trackProgress, setTrackProgress] = useState(0);
  const [sec, setSec] = useState('00');
  const [min, setMin] = useState('00');
  const [time, setTime] = useState('00:00');
  const [duration, setDuration] = useState('00:00');
  const audioRef = useRef(new Audio(props.audioSrc));
  const intervalRef = useRef();

  const currentPercentage = duration
    ? `${(trackProgress / duration) * 100}%`
    : "0%";
  const trackStyling = `
  -webkit-gradient(linear, 0% 0%, 100% 0%, color-stop(${currentPercentage}, #4981ff), color-stop(${currentPercentage}, #a2c3ff))
`;
  const startTimer = () => {
    // Clear any timers already running
    clearInterval(intervalRef.current);

    intervalRef.current = setInterval(() => {
      if (audioRef.current.ended) {
        setIsPlaying(false);
        setTrackProgress(0);
        clearInterval(intervalRef.current);
        setSec('00');
        setMin('00')
      } else {
        setTrackProgress(audioRef.current.currentTime);
        let sec = Math.floor(audioRef.current.currentTime);
        let min = Math.floor(sec / 60);
        min = min >= 10 ? min : '0' + min;
        sec = Math.floor(sec % 60);
        sec = sec >= 10 ? sec : '0' + sec;
        setSec(sec);
        setMin(min);
      }
    }, 100);
  };

  const onScrub = (value) => {
    audioRef.current.currentTime = value;
    setTrackProgress(audioRef.current.currentTime);
  };

  const fastForward = () => {
    audioRef.current.currentTime += 10;
  }

  const fastRewind = () => {
    audioRef.current.currentTime -= 10;
  }

  function saveAs() {
    const url = audioRef.current.src;
    var filename = url.substring(url.lastIndexOf("/") + 1).split("?")[0];
    var xhr = new XMLHttpRequest();
    xhr.responseType = 'blob';
    xhr.onload = function () {
      var a = document.createElement('a');
      a.href = window.URL.createObjectURL(xhr.response);
      a.download = filename;
      a.style.display = 'none';
      document.body.appendChild(a);
      a.click();
      a.remove();
    };
    xhr.open('GET', url);
    xhr.send();
  }

  useEffect(() => {
    // audioRef.current.preload = 'metadata'
    audioRef.current.ondurationchange = () => {
      const { duration } = audioRef.current;
      console.log(audioRef.current.href);
      setDuration(duration)
    }
  }, [audioRef]);

  useEffect(() => {
    const min = Math.floor(duration / 60);
    let sec = Math.floor(duration % 60);
    sec = sec >= 10 ? sec : '0' + sec;
    setTime(`${min}:${sec}`)
    setIsPlaying(false);
    setTrackProgress(0);
    setSec('00');
    setMin('00');
    clearInterval(intervalRef.current);
    audioRef.current.src = props.audioSrc;
  }, [props.audioSrc, duration])

  useEffect(() => {
    if (isPlaying) {
      audioRef.current.play();
      startTimer();
    } else {
      clearInterval(intervalRef.current)
      audioRef.current.pause();
    }
  }, [isPlaying]);

  useEffect(() => {
    const curr = audioRef.current;
    // Pause and clean up on unmount
    return () => {
      curr.pause();
      clearInterval(intervalRef.current);
    };
  }, []);

  return (
    <Box className={"controls" + (props.extended ? ' ap-ext' : ' audio-player ')} display="flex">
      <input
        dir="ltr"
        value={trackProgress}
        type="range"
        className="progress range"
        step="1"
        min="0"
        max={duration ? duration : `${duration}`}
        onChange={(e) => onScrub(e.target.value)}
        style={{ background: trackStyling }}

      />

      <div xs={1}>
        <IconButton className="btn" onClick={saveAs}>
          <GetAppIcon className="btn" />
        </IconButton>
      </div>
      <div xs={1}>
        <p className="time">{time}</p>
      </div>
      <div xs={1}>
        <IconButton className="btn">
          <Forward10Icon onClick={fastForward} />
        </IconButton>
      </div>
      {!isPlaying ? <div xs={1} >
        <IconButton className="btn" onClick={() => setIsPlaying(true)}>
          <PlayCircleOutlineIcon />
        </IconButton>
      </div> :
        <div xs={1} >
          <IconButton className="btn" onClick={() => setIsPlaying(false)}>
            <PauseCircleOutlineIcon />
          </IconButton>
        </div>}
      <div xs={1}>
        <IconButton className="btn">
          <Replay10Icon onClick={fastRewind} />
        </IconButton>
      </div>
      <div xs={1}>
        <p className="time">{min + ':' + sec}</p>
      </div>
    </Box>
  )
}