import React, { useState, useEffect } from 'react';
import {
  EmailShareButton,
  FacebookShareButton,
  TelegramShareButton,
  TwitterShareButton,
  WhatsappShareButton,

} from "react-share";
import {
  EmailIcon,
  FacebookIcon,
  TelegramIcon,
  TwitterIcon,
  WhatsappIcon,
} from "react-share";
import AudioPlayer from '../audioPlayer/AudioPlayer';
import { Grid, Button, Menu, MenuItem, IconButton } from '@material-ui/core';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import FullWindow from "./FullWindow";
import './sicha.css';

export default function Sicha(props) {
  const [lang, setLang] = useState('y');
  const [abst, setAbst] = useState();
  const [title, setTitle] = useState();
  // const [extended, setExtended] = useState(false);
  const [anchorEle, setAnchorEle] = useState(null);
  const url = String(window.location);

  function toggleLang() {
    setLang(state => {
      return state === 'y' ? 'h' : 'y';
    });
  }

  function toggleExtend() {
    props.setExtended(state => !state);
  }

  const handleClick = (event) => {
    console.log(event.currentTarget);
    setAnchorEle(event.currentTarget);
  };

  const handle_Close = () => {
    setAnchorEle(null);
  };

  useEffect(() => {
    setAbst(document.querySelector('.abst')?.innerText + '\n');
    setTitle(document.querySelector('.abst')?.lastElementChild.innerText)
  }, [props.sicha]);

  return (
    <>
      {props.sicha &&
        <Grid container className="cont">
          <Grid className={props.extended ? "dash dash-ext" : "dash"} item xs={12} lg={4}>
            <div style={{ display: props.extended ? 'none' : 'block' }}  className="img"></div>
            <h3 style={{ display: props.extended ? 'none' : 'block' }} ></h3>
            <div className="abst" style={{ display: props.extended ? 'none' : 'block' }}  dangerouslySetInnerHTML={{ __html: props.sicha.abstract }}></div>
            {/* <Button className="dash-btn" variant="contained" color="primary" onClick={() => toggleLang()}>{lang === 'y' ? ' "' : ' '}</Button> */}
            <AudioPlayer extended={props.extended} audioSrc={props.sicha.recUrl} />
          </Grid>
          <Grid item xs={12} lg={props.extended ? 12 : 8} className={props.extended ? "pdf-wrapper-ext" : "pdf-wrapper"}>
            <Button className="lang-btn" variant="contained" color="primary" onClick={() => toggleLang()}>{lang === 'y' ? ' "' : ' '}</Button>
            {lang === 'y' ?
              <FullWindow className="pdf" lang={lang} url={props.sicha.content} /> :
              <FullWindow className="pdf" lang={lang} url={props.sicha.contentHeb} />}
            {/* {extended && <AudioPlayer extended={extended} audioSrc={props.sicha.recUrl} />} */}
            <IconButton className={props.extended ? "ext-btn-ext" : "ext-btn"} color="primary" onClick={toggleExtend}>
              {props.extended ? <ChevronLeftIcon /> :
              <ChevronRightIcon />}
            </IconButton>
          </Grid>
        </Grid>}
      <Button className="share">
        <svg onClick={handleClick} xmlns="http://www.w3.org/2000/svg" width="100%" height="100%" fill="currentColor" class="bi bi-share" viewBox="0 0 16 16">
          <path d="M13.5 1a1.5 1.5 0 1 0 0 3 1.5 1.5 0 0 0 0-3zM11 2.5a2.5 2.5 0 1 1 .603 1.628l-6.718 3.12a2.499 2.499 0 0 1 0 1.504l6.718 3.12a2.5 2.5 0 1 1-.488.876l-6.718-3.12a2.5 2.5 0 1 1 0-3.256l6.718-3.12A2.5 2.5 0 0 1 11 2.5zm-8.5 4a1.5 1.5 0 1 0 0 3 1.5 1.5 0 0 0 0-3zm11 5.5a1.5 1.5 0 1 0 0 3 1.5 1.5 0 0 0 0-3z" />
        </svg>
        <Menu
          disableScrollLock={true}
          className="menu"
          id="simple-menu"
          anchorEl={anchorEle}
          keepMounted
          open={Boolean(anchorEle)}
          onClose={handle_Close}
        >
          <MenuItem onClick={handle_Close}>
            <FacebookShareButton
              url={url}
              quote={abst}
            >
              <FacebookIcon size={32} round={true} />
            </FacebookShareButton>
          </MenuItem>

          <MenuItem onClick={handle_Close}>
            <TwitterShareButton
              title={abst}
              hashtags={["The_Daily_Sicha", "_"]}
              url={url}
            >
              <TwitterIcon size={32} round={true} />
            </TwitterShareButton>
          </MenuItem>

          <MenuItem onClick={handle_Close}>
            <EmailShareButton
              url={url}
              subject={props.sicha?.title || title}
              body={abst}
            >
              <EmailIcon size={32} round={true} />
            </EmailShareButton>
          </MenuItem>

          <MenuItem onClick={handle_Close}>
            <TelegramShareButton
              title={props.sicha?.title || title}
              url={url}
            >
              <TelegramIcon size={32} round={true} />
            </TelegramShareButton>
          </MenuItem>

          <MenuItem onClick={handle_Close}>
            <WhatsappShareButton
              url={url}
              title={abst}
            >
              <WhatsappIcon size={32} round={true} />
            </WhatsappShareButton>
          </MenuItem>

        </Menu>
      </Button>
    </>
  )
}