import React, { useEffect, useState } from 'react';
import { useForm } from "react-hook-form";
import { useStyles, wordToHTML, submitDays, pdfToText, getDates, DAYS_OF_WEEK, HEBREW_YEARS, HEBREW_NUMS } from './uploadDSService';
import { parseExcel } from '../adminSevice';
import './uploadDS.css';
import { FormControl, InputLabel, Select, Button, TextField, Grid, List, ListItem, ListItemText, ListItemIcon, Box, Typography, Accordion, AccordionSummary, AccordionDetails } from '@material-ui/core/';
import CloseIcon from '@material-ui/icons/Close';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { create } from "jss";
import rtl from "jss-rtl";
import { StylesProvider, jssPreset, createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import Loader from "react-loader-spinner";
import { HDate } from '@hebcal/core';
import { ReactJewishDatePicker, BasicJewishDay } from "react-jewish-datepicker";

const jss = create({ plugins: [...jssPreset().plugins, rtl()] });

const rtlTheme = createMuiTheme({ direction: "rtl" });

export default function UploadDS() {
  const classes = useStyles();
  const { register, handleSubmit, reset, formState: { errors } } = useForm();
  const [year, setYear] = useState(new HDate().getFullYear());
  const [dates, setDates] = useState([]);
  const [date, setDate] = useState();
  const [position, setPosition] = useState();
  const [start, setStart] = useState(1);
  const [end, setEnd] = useState(0);
  const [titles, setTitles] = useState();
  const [days, setDays] = useState();
  const [dedications, setDedications] = useState({});

  const [spinner, setSpinner] = useState(false);
  const [res, setRes] = useState(false);

  useEffect(() => {
    const dates = getDates(year);
    setDates(dates);
    setEnd(dates.length);
  }, [year]);

  const handleInput = (e) => {
    const files = e.target.files;
    const name = e.target.name;
    // if (files.length !== end - (start - 1)) {
    //   alert(`      (${files.length})      (${end - (start - 1)})`)
    //   return;
    // }
    setSpinner(true);
    switch (name) {
      case 'days':
        parseExcel(files).then(res => {
          setDays(res)
          console.log(dates);
          setSpinner(false);
        });
        break;
      case 'title':
        wordToHTML(files).then(res => {
          setTitles(Array.from(res))
          setSpinner(false);
        });
        break;
      case 'cont':
        wordToHTML(files).then(res => {
          setDedications(res);
          setSpinner(false);
        })
        break;
      default:
        setSpinner(false);
        break;
    }
  }

  const onSubmit = (data, e) => {
    setSpinner(true)
    submitDays(days, dates).then(res => {
      console.log(res);
      setSpinner(false)
      alert('  ');
    });
    reset();
  };

  const handleChange = (event) => {
    setYear(event.target.value);
  };

  const removeDate = (num) => {
    console.log(num);
    const d = dates.filter((date, i) => {
      return i !== num;
    });
    setDates(d);
  };

  const handleDatePicker = (day) => {
    const date = new HDate(day.date)
    setDate({
      desc: DAYS_OF_WEEK[date.getDay()] + ' - ' + date.renderGematriya(),
      date: date.greg().toLocaleDateString('he-IL', { year: 'numeric', month: "2-digit", day: "2-digit" }).replace(/\./g, '-')
    })
  }

  const addDate = () => {
    const d = [...dates];
    d.splice(position - 1, 0, date);
    setDates(d);
  }

  const datesList = dates?.map((date, i) => (
    <ListItem key={i + '#'} button className={classes.list}>
      <strong>{i + 1}.</strong><ListItemText primary={date.desc} />
      <ListItemIcon onClick={() => removeDate(i)}>
        <CloseIcon />
      </ListItemIcon>
    </ListItem>
  ));

  const yearsList = HEBREW_YEARS?.map((year, i) => (
    <option key={i + '#'} value={HEBREW_NUMS[i]}>{year}</option>
  ));

  return (
    <Grid container>
      <StylesProvider jss={jss}>
        <ThemeProvider theme={rtlTheme} >
          <Grid item xs={6}>
            <Grid container>
              <Grid item xs={4} className="year">
                <h3> </h3>
                <FormControl variant="outlined" className={classes.formControl}>
                  <InputLabel id="demo-simple-select-outlined-label"></InputLabel>
                  <Select
                    labelId="demo-simple-select-outlined-label"
                    id="demo-simple-select-outlined"
                    value={year}
                    onChange={handleChange}
                    label=""
                    native
                  >
                    {yearsList}
                  </Select>
                </FormControl>
              </Grid>
              {/* <Grid item xs={8} className="year">
                <h3> </h3>
                <Box className="add-date-inner" display="flex" alignContent="center">
                  <TextField
                    label=""
                    type="number"
                    variant="outlined"
                    className="number"
                    value={start}
                    onChange={(e) => setStart(e.target.valueAsNumber)}
                  />
                  <TextField
                    label=" ()"
                    type="number"
                    variant="outlined"
                    className="number"
                    value={end}
                    onChange={(e) => setEnd(e.target.valueAsNumber)}
                  />
                </Box>
              </Grid> */}
              <Grid item xs={8}>
                <Box className="add-date">
                  <h3> </h3>
                  <Box className="add-date-inner" display="flex" alignContent="space-between">
                    <TextField
                      label=""
                      type="number"
                      variant="outlined"
                      className="position"
                      value={position}
                      onChange={(e) => setPosition(e.target.value)}
                    />
                    <Box className="cal">
                      <svg xmlns="http://www.w3.org/2000/svg" width="25" height="25" fill="currentColor" class="bi bi-calendar3" viewBox="0 0 16 16">
                        <path d="M14 0H2a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2zM1 3.857C1 3.384 1.448 3 2 3h12c.552 0 1 .384 1 .857v10.286c0 .473-.448.857-1 .857H2c-.552 0-1-.384-1-.857V3.857z" />
                        <path d="M6.5 7a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm3 0a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm3 0a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm-9 3a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm3 0a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm3 0a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm3 0a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm-9 3a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm3 0a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm3 0a1 1 0 1 0 0-2 1 1 0 0 0 0 2z" />
                      </svg>
                      <ReactJewishDatePicker
                        isHebrew
                        onClick={(day) => {
                          handleDatePicker(day);
                        }}
                      />
                    </Box>
                    <div className="picked-date">{date?.desc}</div>
                    <Button
                      className="add-btn"
                      variant="contained"
                      color="primary"
                      onClick={addDate}
                      disabled={!date || !position}
                    ></Button>
                  </Box>
                </Box>
              </Grid>
            </Grid>
          </Grid>
          <Grid item xs={6} className={classes.upload}>
            <h3> </h3>
            <form className={classes.form} onChange={handleInput} onSubmit={handleSubmit(onSubmit)}>
              <Box display="flex" justifyContent="center" alignItems="center">
                <TextField
                  label=""
                  name="days"
                  type="file"
                  variant="outlined"
                />
                <Button
                  type="submit"
                  variant="contained"
                  color="primary"
                > </Button>
              </Box>
              <Box display="flex" justifyContent="center" alignItems="center">
                <TextField
                  label=""
                  name="title"
                  type="file"
                  inputProps={{
                    "multiple": true,
                  }}
                  variant="outlined"
                />
                <Button
                  type="submit"
                  variant="contained"
                  color="primary"
                > </Button>
              </Box>
              <Box display="flex" justifyContent="center" alignItems="center">
                <TextField
                  label=""
                  name="dedications"
                  type="file"
                  inputProps={{
                    "multiple": true,
                  }}
                  variant="outlined"
                />
                <Button
                  type="submit"
                  variant="contained"
                  color="primary"
                > </Button>
              </Box>
            </form>
          </Grid>
        </ThemeProvider>
      </StylesProvider>
      <Grid item xs={12} >
        <Accordion>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon className="accordion" />}
            aria-controls="panel1a-content"
            id="panel1a-header"
            className="accordion"
          >
            <h4>   <span>{dates?.length}</span> .     .</h4>
          </AccordionSummary>
          <AccordionDetails>
            <List className="list">
              {datesList}
            </List>
          </AccordionDetails>
        </Accordion>

      </Grid>
      <div className="spinner-wrapper" style={spinner ? { display: 'block' } : { display: 'none' }}>
        <Loader
          type="ThreeDots"
          color="#008080"
          height={100}
          width={100}
          visible={spinner}
        />
      </div>
    </Grid >
  );
}
