/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from 'react';
import { HDate } from '@hebcal/core';
import { setDateAtFirst, getDailySicha, getEvent, DAYS_OF_WEEK } from './DatePickerService';
import { Box, Button, Typography, Grid, useMediaQuery } from '@material-ui/core';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';

import { useHistory } from "react-router-dom";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import { ReactJewishDatePicker } from "react-jewish-datepicker";
import './datePicker.css';

export default function DatePicker(props) {
  const [date, setDate] = useState(setDateAtFirst());
  const [dateDesc, setDateDesc] = useState();
  const [greg, setGreg] = useState(date.greg());
  const matches = useMediaQuery('(min-width:425px)');
  const history = useHistory();

  useEffect(() => {
    props.setSpinner(true);
    setDateAtFirst()
    getSicha()
    const historyListener = history.listen(() => {
      if (setDateAtFirst().renderGematriya() !== date.renderGematriya()) {
        setDate(setDateAtFirst())
      }
      getSicha();
    });
    return () => {
      historyListener();
    }
  }, []);

  useEffect(() => {
    if (!window.location.search) {
      convertDateToGreg(date)
    }
    if (!props.sicha) {
      const DOW = date.getDay();
      const shabat = date.onOrAfter(6);
      const parasha = getEvent(shabat);
      setDateDesc(DAYS_OF_WEEK[DOW] + ' ' + parasha + " " + date?.renderGematriya());
    }
  }, [date]);

  const getSicha = () => {
    getDailySicha(window.location.search.split('=')[1]).then(({sicha, day}) => {
      console.log(sicha, day);
      if (sicha) {
        props.setNotFound(false);
        props.getSicha(sicha)
        setDateDesc(day.title)
      } else {
        props.setNotFound(true);
      }
      props.setSpinner(false)

    }).catch(() => {
      props.setSpinner(false)
    });
  }

  const handleNext = () => {
    props.setSpinner(true)
    const next = date.next();
    convertDateToGreg(next)
  }

  const handlePrev = () => {
    const prev = date.prev();
    convertDateToGreg(prev)
  }

  const handleDatePicker = (day) => {
    const date = new HDate(day.date)
    convertDateToGreg(date)
  }

  const convertDateToGreg = (date) => {
    setDate(date)
    setGreg(date.greg());
    const format = { year: 'numeric', month: "2-digit", day: "2-digit" };
    const gregDate = date.greg().toLocaleDateString('he-IL', format).replace(/\./g, '-');
    history.push(`?date=${gregDate}`);
  }

  return (
    <Grid container className="date-wrapper" alignContent="center">
      <Grid lg={props.extended ? 0 : 4} xs={12} item></Grid>
      <Grid lg={props.extended ? 12 : 8} xs={12} item>
        <Grid container justify="space-around" className={props.extended ? "date-picker-extended date-picker" : "date-picker"}>
          <Button className="nav-btn" color="primary" onClick={handlePrev}>
            <NavigateNextIcon className="nav-icon" />
            <Box display={matches ? 'block' : 'none'}></Box>
          </Button>
          <Box display="flex" alignItems="center">
            <Typography variant="h6" className="date">{dateDesc}</Typography>
            <Box className="cal" display={matches ? 'block' : 'none'}>
              <svg xmlns="http://www.w3.org/2000/svg" width="25" height="25" fill="#011746" class="bi bi-calendar3" viewBox="0 0 16 16">
                <path d="M14 0H2a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2zM1 3.857C1 3.384 1.448 3 2 3h12c.552 0 1 .384 1 .857v10.286c0 .473-.448.857-1 .857H2c-.552 0-1-.384-1-.857V3.857z" />
                <path d="M6.5 7a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm3 0a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm3 0a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm-9 3a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm3 0a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm3 0a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm3 0a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm-9 3a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm3 0a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm3 0a1 1 0 1 0 0-2 1 1 0 0 0 0 2z" />
              </svg>
              <ReactJewishDatePicker
                value={greg}
                isHebrew
                onClick={(day) => {
                  handleDatePicker(day);
                }}
              />
            </Box>
          </Box>
          <Button color="primary" onClick={handleNext}>
            <Box display={matches ? 'block' : 'none'}></Box>
            <NavigateBeforeIcon className="nav-icon" />
          </Button>
        </Grid>
      </Grid>
    </Grid>
  )
}