import { makeStyles } from '@material-ui/core/styles';
import axios from 'axios';

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

async function submit(fullName, address, email, phone, dedicationsValue, notes) {
  console.log('sub');
  return await axios.post(`${process.env.REACT_APP_API_ENDPOINT}/dedication/add-dedication`, {
    fullName, address, email, phone, dedications: dedicationsValue, notes
  });
}

export { useStyles, submit } 