import React, { useState } from 'react';
import { useForm } from "react-hook-form";

import { useStyles, wordToHTML, submit, setFilesObj } from './uploadTopicsService';
import './uploadTopics.css';
import { Button, TextField, Box } from '@material-ui/core/';
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import Loader from "react-loader-spinner";
import { create } from "jss";
import rtl from "jss-rtl";
import { StylesProvider, jssPreset, createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
const jss = create({ plugins: [...jssPreset().plugins, rtl()] });

const rtlTheme = createMuiTheme({ direction: "rtl" });

export default function UploadTopics() {
  const classes = useStyles();
  const { register, handleSubmit, reset, formState: { errors } } = useForm();
  const [topic, setTopic] = useState('');
  const [abstracts, setAbstrats] = useState();
  const [content, setContent] = useState();
  const [contentHeb, setContentHeb] = useState({});
  const [records, setRecords] = useState();
  const [spinner, setSpinner] = useState(false);


  const handleInput = (e) => {
    const files = e.target.files;
    const name = e.target.name;
    setSpinner(true);
    switch (name) {
      case 'topic':
        setTopic(e.target.value)
        setSpinner(false);
        break;
      case 'abst':
        wordToHTML(files).then(res => {
          setAbstrats(res)
          setSpinner(false);
        });
        break;
      case 'cont':
        const sichos = setFilesObj(files);
        setContent(sichos)
        setSpinner(false);
        break;
      case 'contHeb':
        const sichosHeb = setFilesObj(files);
        setContentHeb(sichosHeb)
        setSpinner(false);
        break;
      case 'recs':
        const recs = setFilesObj(files);
        setRecords(recs);
        setSpinner(false);
        break;
      default:
        setSpinner(false);
        break;
    }
  }

  const onSubmit = () => {
    submit(topic, abstracts, content, contentHeb, records);
    reset();
  };

  return (
    <div className={classes.root}>
      <div>
        <StylesProvider jss={jss}>
          <ThemeProvider theme={rtlTheme} >
            <Box width="100%" textAlign="center">
              <h3> </h3>
              <form className={classes.form} onChange={handleInput} onSubmit={handleSubmit(onSubmit)}>
                <Box display="flex" justifyContent="center" alignItems="center">
                  <TextField
                    label=""
                    name="topic"
                    type="text"
                    value={topic}
                    variant="outlined"
                    {...register("topic", { required: true })}
                    style={errors.topic && { border: '1px solid red', borderRadius: '5px' }}
                  />
                </Box>
                <Box display="flex" justifyContent="center" alignItems="center">
                  <TextField
                    label=""
                    name="abst"
                    type="file"
                    inputProps={{
                      "multiple": true,
                    }}
                    variant="outlined"
                    {...register("abst", { required: true })}
                    style={errors.abst && { border: '1px solid red', borderRadius: '5px' }}
                  />
                </Box>
                <Box display="flex" justifyContent="center" alignItems="center">
                  <TextField
                    label=" "
                    name="cont"
                    type="file"
                    inputProps={{
                      "multiple": true,
                    }}
                    variant="outlined"
                    {...register("cont", { required: true })}
                    style={errors.cont && { border: '1px solid red', borderRadius: '5px' }}
                  />
                </Box>
                <Box display="flex" justifyContent="center" alignItems="center">
                  <TextField
                    label=" "
                    name="contHeb"
                    type="file"
                    inputProps={{
                      "multiple": true,
                    }}
                    variant="outlined"
                    {...register("contHeb", { required: true })}
                    style={errors.contHeb && { border: '1px solid red', borderRadius: '5px' }}
                  />
                </Box>
                <Box display="flex" justifyContent="center" alignItems="center">
                  <TextField
                    label=" "
                    name="recs"
                    type="file"
                    inputProps={{
                      "multiple": true,
                    }}
                    variant="outlined"
                    {...register("recs", { required: true })}
                    style={errors.recs && { border: '1px solid red', borderRadius: '5px' }}
                  />
                </Box>
                <Box display="flex" justifyContent="center" alignItems="center">
                  <Button
                    type="submit"
                    variant="contained"
                    color="primary"
                  ></Button>
                </Box>
              </form>
            </Box>
          </ThemeProvider>
        </StylesProvider>
      </div>
      <div className="spinner-wrapper" style={spinner ? { display: 'block' } : { display: 'none' }}>
        <Loader
          type="ThreeDots"
          color="#008080"
          height={100}
          width={100}
          visible={spinner}
        />
      </div>
    </div>
  );
}
