import { makeStyles, fade } from '@material-ui/core/styles';
import axios from 'axios';


const useStyles = makeStyles((theme) => ({
  root: {
    textAlign: 'center',
  },
  formControl: {
    margin: theme.spacing(3),
    minWidth: '30%',
  },
  searchWrapper: {
    boxShadow: '0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%)',
    '&:hover': {
      boxShadow: '0px 2px 4px -1px rgb(0 0 0 / 20%), 0px 4px 5px 0px rgb(0 0 0 / 14%), 0px 1px 10px 0px rgb(0 0 0 / 12%);,'
    },
    borderRadius: theme.shape.borderRadius,
    border: '1px solid #3f51b5',
    overflow: 'hidden',
  },
  search: {
    position: 'relative',
    '&:hover': {
      backgroundColor: fade('#010101', 0.25),
    },
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: '100%',
    },
  },
  searchIcon: {
    padding: theme.spacing(0, 1),
    height: '100%',
    position: 'absolute',
    left: 0,
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 1),
    paddingLeft: `calc(1em + ${theme.spacing(3)}px)`,
    transition: theme.transitions.create('width'),
    width: '100%',
  },
}));

async function searchSicha(fields, value, page) {
  const { data } = await axios.post(`${process.env.REACT_APP_API_ENDPOINT}/daily-sicha/search-sicha`, { fields, value, page });
  return data;
}

export { useStyles, searchSicha }