import axios from 'axios';

async function getTopicSicha(id) {
  const { data } = await axios.get(`${process.env.REACT_APP_API_ENDPOINT}/topic/get-topic-sicha?id=${id}`);
  return data;
}

async function getSearchSicha(id) {
  const { data } = await axios.get(`${process.env.REACT_APP_API_ENDPOINT}/daily-sicha/get-search-sicha?id=${id}`);
  return data;
}

export { getTopicSicha, getSearchSicha }