function getEvent(date) {
  const options = {
    start: date,
    end: date,
    isHebrewYear: true,
    sedrot: false,
    candlelighting: false,
    noModern: true,
    noMinorFast: true,
    noRoshChodesh: true,
    noSpecialShabbat: true,
  };
  const event = HebrewCalendar.calendar(options);
  return event
}

function getDates(year) {
  const options = {
    year: year,
    isHebrewYear: true,
    candlelighting: false,
    sedrot: false,
    noModern: true,
    addHebrewDates: true,
    noSpecialShabbat: true,
    noMinorFast: true,
    noHolidays: true,
  };
  let dates = HebrewCalendar.calendar(options);
  const holidays = [33, 5, 16389, 41, 13];
  const erevYT = [1048578, 2097154, 2097162];

  dates = dates.filter(date => {
    const mask = getEvent(date.date)[0]?.mask;
    const day = date.date.getDay();
    return !holidays.includes(mask) &&
      day !== 6 &&
      (day !== 5 ||
        erevYT.includes(mask) ||
        (day === 5 && holidays.includes(getEvent(date.date.after(0))[0]?.mask))) &&
      (day !== 4 || !erevYT.includes(mask)) &&
      date.desc !== `29 Elul ${year - 1}`;
  });
  const format = { year: 'numeric', month: "2-digit", day: "2-digit" };
  return dates.map((date => {
    return {
      desc: DAYS_OF_WEEK[date.date.getDay()] + " - " + date.date.renderGematriya(),
      date: date.date.greg().toLocaleDateString('he-IL', format).replace(/\./g, '-'),
    }
  }));
}


function wordToHTML(files) {
  return new Promise(resolve => {
    const arr = [];
    Array.from(files).forEach((file, i) => {
      const reader = new FileReader();
      reader.onloadend = async function () {
        const res = await mammoth.convertToHtml({ arrayBuffer: reader.result });
        // const splited = res.value.replace('<h1>', '').split('</h1>')
        arr.push(res.value)
        if (i + 1 === files.length) {
          resolve(arr);
        }
      }
      reader.readAsArrayBuffer(files[i]);
    });
  });
}

const getPageText = async (pdf, pageNo) => {
  const page = (await pdf).getPage(pageNo);
  const tokenizedText = (await page).getTextContent();
  let pageText = (await tokenizedText).items.map(token => token.str).join("");
  pageText = pageText.replace(/([\u05B0-\u05BD]|[\u05BF-\u05C7])/g, "");
  pageText = pageText.replace(/\u05BE/g, " ").replace(/[\u05B0-\u05C7]/g, "");
  pageText = pageText.replace("<h4></h4>", "");
  return pageText;
};

function pdfToText(files) {
  return new Promise(resolve => {
    const obj = {};
    const arr = [];
    Array.from(files).forEach((file, i) => {
      const reader = new FileReader();
      reader.onloadend = async function () {
        const pdf = pdfjs.getDocument(reader.result).promise;
        const maxPages = (await pdf).numPages
        const pageTextPromises = [];
        for (let pageNo = 1; pageNo <= maxPages; pageNo++) {
          pageTextPromises.push(getPageText(pdf, pageNo));
        }
        const pageTexts = await Promise.all(pageTextPromises);
        console.log(pageTexts);
        arr.push(pageTexts[0])
        obj[file.name.split('.')[0]] = pageTexts[0];
        if (i + 1 === files.length) {
          resolve(arr);
        }
      }
      reader.readAsArrayBuffer(files[i]);
    });
  });
}

