async function submitDays(days, dates) {
  days = days.map((day, i) => {
    return {
      id: day.ID,
      date: dates[day.day - 1].date,
      title: day.title,
    }
  })
  return await axios.post(`${process.env.REACT_APP_API_ENDPOINT}/daily-sicha/add-sichos-dates`, days);
}



