import React, { useState, useEffect } from 'react';
import { Accordion, AccordionDetails, AccordionSummary, Table, TableBody, TableHead, TableContainer, TableRow, TableCell, Typography, Paper, Collapse, Box, IconButton, Grid } from '@material-ui/core/';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { useStyles, getDedications } from './dedicationsService';
import './dedications.css';
import { withStyles } from '@material-ui/core/styles';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import { makeStyles } from '@material-ui/core/styles';
import { create } from "jss";
import rtl from "jss-rtl";
import { StylesProvider, jssPreset, createMuiTheme, ThemeProvider } from '@material-ui/core/styles';

const jss = create({ plugins: [...jssPreset().plugins, rtl()] });
const rtlTheme = createMuiTheme({ direction: "rtl" });

export default function Dedications() {
  const classes = useStyles();
  const [dedications, setDedications] = useState([]);
  const [expanded, setExpanded] = React.useState(false);

  const StyledTableCell = withStyles((theme) => ({
    head: {
      backgroundColor: theme.palette.common.black,
      color: theme.palette.common.white,
      // direction: 'rtl',
    },
    body: {
      fontSize: 14,
      // direction: 'rtl',
    },
  }))(TableCell);

  const useRowStyles = makeStyles({
    root: {
      '& > *': {
        borderBottom: 'unset',
      },
    },
  });

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  useEffect(() => {
    getDedications().then(res => {
      setDedications(res);
    })
  }, []);



  function Row(props) {
    const { row } = props;
    const [open, setOpen] = useState(false);
    const classes = useRowStyles();

    return (
      <>
        <TableRow className="main-row">
          <TableCell>
            <IconButton aria-label="expand row" size="small" onClick={() => setOpen(!open)}>
              {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
            </IconButton>
          </TableCell>
          <TableCell >{row.fullName}</TableCell>
          <TableCell>{row.address}</TableCell>
          <TableCell>{row.email}</TableCell>
          <TableCell>{row.phone}</TableCell>
        </TableRow>
        <TableRow>
          <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
            <Collapse in={open} timeout="auto" unmountOnExit>
              <Box margin={1} className="secondary-table">
                {row.notes && <Grid container>
                  <Grid item xs={1}>:</Grid>
                  <Grid item xs={11}>{row.notes}</Grid>
                </Grid>}
                <Table size="small">
                  <TableHead>
                    <TableRow>
                      <TableCell>#</TableCell>
                      <TableCell></TableCell>
                      <TableCell></TableCell>
                      <TableCell></TableCell>
                      <TableCell> /</TableCell>
                      <TableCell></TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {row.dedications.map((d, i) => (
                      <TableRow key={d.date}>
                        <TableCell>
                          {i + 1}
                        </TableCell>
                        <TableCell>{d.date}</TableCell>
                        <TableCell>{d.event}</TableCell>
                        <TableCell>{d.name}</TableCell>
                        <TableCell>{d.parent}</TableCell>
                        <TableCell>{d.donor}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </Box>
            </Collapse>
          </TableCell>
        </TableRow>
      </>
    );
  }

  return (
    <div className={classes.root}>
      <Typography className="ded-head" variant="h4">
        
      </Typography>
      <StylesProvider jss={jss}>
        <ThemeProvider theme={rtlTheme} >
          <TableContainer component={Paper}>
            <Table aria-label="collapsible table">
              <TableHead>
                <TableRow className="heading">
                  <TableCell />
                  <TableCell></TableCell>
                  <TableCell></TableCell>
                  <TableCell>"</TableCell>
                  <TableCell></TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {dedications?.map((row) => (
                  <Row key={row.name} row={row} />
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </ThemeProvider>
      </StylesProvider>
    </div>
  );
}
