/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from 'react';
import { InputBase, FormLabel, FormControl, FormGroup, FormControlLabel, FormHelperText, Checkbox, Button, Box } from '@material-ui/core/';
import SearchIcon from '@material-ui/icons/Search';
import { useStyles, searchSicha } from './SearchInputService';
import './searchInput.css';

export default function SearchInput(props) {
  const classes = useStyles();

  const [value, setValue] = useState('');
  const [fields, setFields] = useState({
    abstract: true,
    contentText: false,
    contentHebText: false,
  });

  useEffect(() => {
    const searchData = JSON.parse(sessionStorage.getItem('search'));
    if (searchData) {
      setValue(() => searchData.value);
      setFields(() => searchData.fields);
    }
  }, []);
  useEffect(() => {
    if (value) {
      search();
    } else {
      props.getSichos([]);
      props.getCount(0);
      sessionStorage.removeItem('search');
    }
  }, [value]);

  useEffect(() => {
    search();
  }, [props.page])

  const handleSearchChange = (e) => {
    setValue(() => e.target.value);
  };

  const handleChange = (event) => {
    setFields({ ...fields, [event.target.name]: event.target.checked });
  };

  const search = () => {
    const checked = Object.keys(fields).filter(f => fields[f])
    if (value) {
      searchSicha(checked, value, props.page - 1).then(({ sichos, count }) => {
        props.getSichos(sichos)
        props.getCount(count)
        const searchData = { fields, value };
        sessionStorage.setItem('search', JSON.stringify(searchData));
      })
    }
  };

  const { abstract, contentText, contentHebText } = fields;
  const error = [abstract, contentText, contentHebText].filter((v) => v).length === 0;

  return (

    <div className={classes.root}>
      <FormControl component="fieldset" className={classes.formControl}>
        <Box className={classes.searchWrapper} display="flex">
          <div className={classes.search}>
          <div className={classes.searchIcon}>
            <SearchIcon />
          </div>
            <InputBase
              placeholder="..."
              classes={{
                root: classes.inputRoot,
                input: classes.inputInput,
              }}
              onChange={handleSearchChange}
              value={value}
              inputProps={{ 'aria-label': 'search' }}
            />
          </div>



        </Box>
        <FormGroup row>
          <FormLabel className="s-label" component="legend"> :</FormLabel>

          <FormControlLabel
            control={<Checkbox
              checked={abstract}
              onChange={handleChange}
              name="abstract"
              color="primary"
            />}
            label=""
          />
          <FormControlLabel
            control={<Checkbox
              checked={contentText}
              onChange={handleChange}
              name="contentText"
              color="primary"
            />}
            label=""
          />
          <FormControlLabel
            control={<Checkbox
              checked={contentHebText}
              onChange={handleChange}
              name="contentHebText"
              color="primary"
            />}
            label='"'
          />
        </FormGroup>
        <FormHelperText>{error ? '    ' : ''}</FormHelperText>
      </FormControl>
      <div className="results">{value && <span>{props.count}   <span className="term">"{value}"</span></span>}</div>

    </div>
  );
}