import React, { useEffect } from 'react';
import ViewSDKClient from './ViewSDKClient';

const previewConfig = {
    showAnnotationTools: false,
    showLeftHandPanel: false,
    defaultViewMode: 'FIT_WIDTH',
    dockPageControls: false
}

function FullWindow(props) {
    useEffect(() => {
        const viewSDKClient = new ViewSDKClient(props.url);
        viewSDKClient.ready().then(() => {
            /* Invoke file preview */
            /* By default the embed mode will be Full Window */
            viewSDKClient.previewFile("pdf-div", previewConfig);
        });
    }, [props.lang, props.url]);

    return <div id="pdf-div" className="full-window-div" />;
}

export default FullWindow;
