import axios from 'axios';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  root: {
    height: '100%',
    verticalAlign: 'top',
    backgroundColor: '#eceeff',
    border: '1px solid #001746',
  },
  link: {
    textDecoration: 'none',
  },
  content: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "start",
    textAlign: 'justify',
    height: '100%',
  },
}));

async function getTopics (id) {
  const { data } = await axios.get(`${process.env.REACT_APP_API_ENDPOINT}/topic/get-topic?id=${id}`);
  return data;
}

export { getTopics, useStyles }