import { makeStyles } from '@material-ui/core/styles';
import mammoth from 'mammoth';
import axios from 'axios';
import { pdfjs } from 'react-pdf';

pdfjs.GlobalWorkerOptions.workerSrc = `https://cdnjs.cloudflare.com/ajax/libs/pdf.js/2.6.347/pdf.worker.js`;

const useStyles = makeStyles((theme) => ({
  form: {
    '& .MuiTextField-root': {
      margin: theme.spacing(1),
      width: '60ch',
      display: 'flex',
    },
    marginBottom: '15px',
  },
  button: {
    marginRight: theme.spacing(1),
    height: 'fit-content',
  },
}));

function wordToHTML(files) {
  return new Promise(resolve => {
    const arr = [];
    Array.from(files).forEach((file, i) => {
      const reader = new FileReader();
      reader.onloadend = async function () {
        const res = await mammoth.convertToHtml({ arrayBuffer: reader.result });
        // const splited = res.value.replace('<h1>', '').split('</h1>')
        arr.push({
          id: file.name.replace('.docx', ''),
          abstract: res.value,
        })
        if (i + 1 === files.length) {
          resolve(arr);
        }
      }
      reader.readAsArrayBuffer(files[i]);
    });
  });
}

const getPageText = async (pdf, pageNo) => {
  const page = (await pdf).getPage(pageNo);
  const tokenizedText = (await page).getTextContent();
  let pageText = (await tokenizedText).items.map(token => token.str).join("");
  pageText = pageText.replace(/([\u05B0-\u05BD]|[\u05BF-\u05C7])/g, "");
  pageText = pageText.replace(/\u05BE/g, " ").replace(/[\u05B0-\u05C7]/g, "");
  pageText = pageText.replace("<h4></h4>", "");
  return pageText;
};

function pdfToText(files) {
  return new Promise(resolve => {
    const obj = {};
    const arr = [];
    Array.from(files).forEach((file, i) => {
      const reader = new FileReader();
      reader.onloadend = async function () {
        const pdf = pdfjs.getDocument(reader.result).promise;
        const maxPages = (await pdf).numPages
        const pageTextPromises = [];
        for (let pageNo = 1; pageNo <= maxPages; pageNo++) {
          pageTextPromises.push(getPageText(pdf, pageNo));
        }
        const pageTexts = await Promise.all(pageTextPromises);
        console.log(pageTexts);
        arr.push(pageTexts[0])
        obj[file.name.split('.')[0]] = pageTexts[0];
        if (i + 1 === files.length) {
          resolve(arr);
        }
      }
      reader.readAsArrayBuffer(files[i]);
    });
  });
}

async function submitData(files) {
  const { data } = await axios.get(`${process.env.REACT_APP_API_ENDPOINT}/daily-sicha/get-base-url`);
  const arr = files.map(row => {
    return {
      id: row.ID,
      idA: row.IDa,
      parshaNum: row[' #'],
      parsha: row[''],
      weekDay: row[' '],
      monthNum: row[' #'],
      month: row[''],
      MonthDay: row[' '],
      Issues: row[''],
      rambamLesson: row[' "'],
      rambamHalacha: row[`" '`],
      deliveredDate: row[' '],
      deliveredYear: row[''],
      content: `${data}/public/pdf/${row.ID}.pdf`,
      contentHeb: `${data}/public/pdfHeb/${row.ID}.pdf`,
      recUrl: `${data}/public/records/${row.ID}.mpeg`,
    }
  });
  await axios.post(`${process.env.REACT_APP_API_ENDPOINT}/daily-sicha/add-sichos-data`, { docs: arr }).then(res => {
    console.log(res);
  });
}

async function submitAbst(abst) {
  const formData = new FormData();
  formData.append('docs', JSON.stringify(abst));
  return await axios.post(`${process.env.REACT_APP_API_ENDPOINT}/daily-sicha/add-sichos-files`, formData);
}

async function submitCont(cont, contText) {
  console.log(cont);
  const arr = contText.map((el, i) => {
    return {
      id: cont[i].name.replace('.pdf', ''),
      contentText: el
    };
  })
  cont = Object.values(cont).map(val => val);
  const formData = new FormData();
  formData.append('docs', JSON.stringify(arr));
  cont.forEach(file => {
    formData.append('cont', file, file.name);
  });

  return await axios.post(`${process.env.REACT_APP_API_ENDPOINT}/daily-sicha/add-sichos-files`, formData);
}

