async function submitRecs(recs) {
  recs = Object.values(recs).map(val => val);
  const formData = new FormData();
  recs.forEach(file => {
    formData.append('recs', file, file.name);
  });

  return await axios.post(`${process.env.REACT_APP_API_ENDPOINT}/daily-sicha/add-sichos-files`, formData);
}


export { useStyles, wordToHTML, submitData, submitAbst, submitCont, submitContHeb, submitRecs, pdfToText };