import { HDate } from '@hebcal/core';
import axios from 'axios';
import { HebrewCalendar } from '@hebcal/core';

const DAYS_OF_WEEK = [" '", " '", " '", " '", " '", " '", " "]

const setDateAtFirst = () => {
  if (window.location.search && window.location.search.startsWith('?date')) {
    const search = window.location.search.split('=')[1].split('-');
    return new HDate(new Date(search[2], search[1] - 1, search[0]));
  } else {
    return new HDate()
  }
}

async function getDailySicha(date) {
  const { data } = await axios.get(`${process.env.REACT_APP_API_ENDPOINT}/daily-sicha/get-Daily-Sicha?date=${date}`);
  return data;
}

function getEvent(date) {
  const options = {
    start: date,
    end: date,
    isHebrewYear: true,
    sedrot: true,
    candlelighting: false,
    addHebrewDates: true,
    noModern: true,
    noMinorFast: true,
    noRoshChodesh: true,
    noSpecialShabbat: true,
  };
  const event = HebrewCalendar.calendar(options).filter(ev => {
    return ev.mask === 1024;
  });
  return event[0].render('he');
}

export { setDateAtFirst, getDailySicha, getEvent, DAYS_OF_WEEK }