import React, { useEffect, useState } from 'react';
import './App.css';
import "fontsource-roboto";
import Header from './components/header/Header';
import Footer from './components/footer/Footer';
import RouteConfig from './navigation/RouterConfig';
import { BrowserRouter as Router } from "react-router-dom";
import { isAuthenticated } from './components/login/loginService';
import Loader from "react-loader-spinner";

function App() {
  const [auth, setAuth] = useState(false);
  const [spinner, setSpinner] = useState(false);

  useEffect(() => {
    setAuth(isAuthenticated());
  }, []);

  return (
    <Router>
      <Header auth={auth} setAuth={setAuth} />
      <RouteConfig setSpinner={setSpinner} setAuth={setAuth} />
      <Footer />
      <div className="spinner-wrapper" style={spinner ? { display: 'block' } : { display: 'none' }}>
        <Loader
          type="ThreeDots"
          color="#4d88ff"
          height={100}
          width={100}
          visible={spinner}
        />
      </div>
    </Router>
  );
}

export default App;
