import React from 'react';
import { Card, CardActionArea, CardContent, Grid, Typography, Button } from '@material-ui/core/';
import { useStyles } from './SearchResultsService';
import { Link } from 'react-router-dom';

import './searchResults.css';


export default function SearchResults(props) {
  const classes = useStyles();

  const sichosList = props.sichos?.map(sicha => (
    <Grid item xs={4} key={sicha._id}>
      <Link className={classes.link} to={`/?sicha=${sicha._id}`}>
        <Card className={classes.root}>
          <CardActionArea className={classes.content}>
            <CardContent>
              <h4></h4>
              <Typography className="text" variant="body2" color="textSecondary" component="p"
                dangerouslySetInnerHTML={{ __html: sicha.abstract }}
              >
              </Typography>
            </CardContent>
            <div className="press">  </div>
          </CardActionArea>
        </Card>
      </Link>
    </Grid>
  ));

  return (
    <>
      <Grid container spacing={3} className="search-cont">
        {sichosList}
      </Grid>
    </>
  );
}