import React, { useRef, useState } from 'react';
import { useForm } from "react-hook-form";
import { useStyles, wordToHTML, submitData, submitAbst, submitCont, submitContHeb, submitRecs } from './uploadSichosFilesService';
import { parseExcel, pdfToText } from '../adminSevice';
import './uploadSichosFiles.css';
import { Button, TextField, Box, } from '@material-ui/core/';
import { create } from "jss";
import rtl from "jss-rtl";
import { StylesProvider, jssPreset, createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import Loader from "react-loader-spinner";

const jss = create({ plugins: [...jssPreset().plugins, rtl()] });

const rtlTheme = createMuiTheme({ direction: "rtl" });

export default function UploadSichosFiles() {
  const classes = useStyles();
  const { register, handleSubmit, reset, formState: { errors } } = useForm();
  const [data, setData] = useState([]);
  const [abstracts, setAbstracts] = useState([]);
  const [contentFiles, setContentFiles] = useState([]);
  const [contentText, setContentText] = useState([]);
  const [contentHebFiles, setContentHebFiles] = useState([]);
  const [contentHebText, setContentHebText] = useState([]);
  const [records, setRecords] = useState([]);
  const [spinner, setSpinner] = useState(false);
  const inputEl = useRef(null);

  const handleInput = (e) => {
    const files = e.target.files;
    const name = e.target.name;
    inputEl.current = e.currentTarget;
    setSpinner(true);
    switch (name) {
      case 'data':
        parseExcel(files).then(res => {
          setData(res);
          setSpinner(false);
        });
        break;
      case 'abst':
        wordToHTML(files).then(res => {
          setAbstracts(Array.from(res));
          setSpinner(false);
        });
        break;
      case 'cont':
        setContentFiles(Array.from(files));
        pdfToText(files).then(res => {
          console.log(res);
          setContentText(res);
          setSpinner(false);
        })
        break;
      case 'contHeb':
        setContentHebFiles(Array.from(files));
        pdfToText(files).then(res => {
          setContentHebText(res);
          setSpinner(false);
        })
        break;
      case 'recs':
        setRecords(Array.from(files));
        setSpinner(false);
        break;
      default:
        setSpinner(false);
        break;
    }
  }

  const onSubmit = (e) => {
    const id = e.currentTarget.id;
console.log(id);
    setSpinner(true);
    switch (id) {
      case 'data':
        submitData(data).then(res => {
          console.log(res);
          setSpinner(false);
          inputEl.current.value = '';
          alert('  ');
        });
        break;
      case 'abst':
        submitAbst(abstracts).then(res => {
          console.log(res);
          setSpinner(false);
          reset();
          alert('  ');
        });
        break;
      case 'cont':
        submitCont(contentFiles, contentText).then(res => {
          console.log(res);
          setSpinner(false);
          inputEl.current.value = ""
          alert('  ');
        });
        break;
      case 'contHeb':
        submitContHeb(contentHebFiles, contentHebText).then(res => {
          console.log(res);
          setSpinner(false);
          reset();
          alert('  ');
        });
        break;
      case 'recs':
        submitRecs(records).then(res => {
          console.log(res);
          setSpinner(false);
          reset();
          alert('  ');
        });
        break;
      default:
        setSpinner(false);
        break;
    }
  };

  return (
    <>
      <StylesProvider jss={jss}>
        <ThemeProvider theme={rtlTheme} >
          <Box width="100%" textAlign="center">
            <h3>  </h3>
            <form className={classes.form} onChange={handleInput}>
              <Box display="flex" justifyContent="center" alignItems="center">
                <TextField
                  label=" "
                  name="data"
                  type="file"
                  variant="outlined"
                  onChange={handleInput}
                />
                <Button
                  className={classes.button}
                  type="button"
                  id="data"
                  variant="contained"
                  color="primary"
                  onClick={onSubmit}
                  disabled={!data.length}
                > </Button>
              </Box>
              <Box display="flex" justifyContent="center" alignItems="center">
                <TextField
                  label=""
                  name="abst"
                  type="file"
                  inputProps={{
                    "multiple": true,
                  }}
                  variant="outlined"
                />
                <Button
                  className={classes.button}
                  type="button"
                  id="abst"
                  variant="contained"
                  color="primary"
                  onClick={onSubmit}
                  disabled={!abstracts.length}
                > </Button>
              </Box>
              <Box display="flex" justifyContent="center" alignItems="center">
                <TextField
                  label=" "
                  name="cont"
                  type="file"
                  inputProps={{
                    "multiple": true,
                  }}
                  variant="outlined"
                />
                <Button
                  className={classes.button}
                  type="button"
                  id="cont"
                  variant="contained"
                  color="primary"
                  onClick={onSubmit}
                  disabled={!contentFiles.length}
                > </Button>
              </Box>
              <Box display="flex" justifyContent="center" alignItems="center">
                <TextField
                  label=" "
                  name="contHeb"
                  type="file"
                  inputProps={{
                    "multiple": true,
                  }}
                  variant="outlined"
                />
                <Button
                  className={classes.button}
                  type="button"
                  id="contHeb"
                  variant="contained"
                  color="primary"
                  onClick={onSubmit}
                  disabled={!contentHebFiles.length}
                > </Button>
              </Box>
              <Box display="flex" justifyContent="center" alignItems="center">
                <TextField
                  label=" "
                  name="recs"
                  type="file"
                  inputProps={{
                    "multiple": true,
                  }}
                  variant="outlined"
                />
                <Button
                  className={classes.button}
                  type="button"
                  id="recs"
                  variant="contained"
                  color="primary"
                  onClick={onSubmit}
                  disabled={!records.length}
                > </Button>
              </Box>
            </form>
          </Box>
        </ThemeProvider>
      </StylesProvider>

      <div className="spinner-wrapper" style={spinner ? { display: 'block' } : { display: 'none' }}>
        <Loader
          type="ThreeDots"
          color="#008080"
          height={100}
          width={100}
          visible={spinner}
        />
      </div>
    </>
  );
}
