import React, { useRef, useState } from 'react';
import { useForm } from "react-hook-form";
import { pdfToText } from '../adminSevice';
// import './uploadSichosFiles.css';
import { Button, TextField, Box, } from '@material-ui/core/';
import { create } from "jss";
import rtl from "jss-rtl";
import { StylesProvider, jssPreset, createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import Loader from "react-loader-spinner";
import { makeStyles } from '@material-ui/core/styles';

const jss = create({ plugins: [...jssPreset().plugins, rtl()] });

const rtlTheme = createMuiTheme({ direction: "rtl" });

const useStyles = makeStyles((theme) => ({
  textField: {
    margin: theme.spacing(1),
    width: '60ch',
    display: 'flex',
  },
}));

export default function UploadSichosFiles() {
  const classes = useStyles();
  const [data, setData] = useState('');

  const handleInput = (e) => {
    const files = e.target.files;
    pdfToText(files).then(res => {
      console.log(res);
      setData(res);
    });
  }

  return (
    <>
      <StylesProvider jss={jss}>
        <ThemeProvider theme={rtlTheme} >
          <Box width="100%" textAlign="center">
            <h3>   -pdf</h3>
            <Box classes={classes.form} display="flex" justifyContent="center" alignItems="center">
              <TextField
                label=" "
                name="data"
                type="file"
                variant="outlined"
                onChange={handleInput}
                className={classes.textField}
              />
            </Box>
          </Box>
        </ThemeProvider>
      </StylesProvider>
      {data && <Box marginTop={3} marginBottom={5} border={1} padding={2} borderRadius='5px'>
        {data}
      </Box>}
    </>
  );
}
