import { makeStyles } from '@material-ui/core/styles';
import axios from 'axios';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    paddingTop: theme.spacing(2)
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
  },
}));


async function getDedications(topic, abst, cont, contHeb, rec) {
  const { data } = await axios.get(`${process.env.REACT_APP_API_ENDPOINT}/dedication/get-dedications`);
  return data;
}



export { useStyles, getDedications };
